html, body {
    height:100%;
    padding:0;
    margin:0;
}

body.background {
    background-color: #E2E4E9;
}

table {
    border-collapse: collapse;
    border-spacing: 0;
    text-align: left;
    vertical-align: top;
}

strong, b {
    font-weight: bold;
}

pre {
    white-space: pre-wrap;
    white-space: -moz-pre-wrap;
    white-space: -pre-wrap;
    white-space: -o-pre-wrap;
    word-wrap: break-word;
}

fieldset, img, abbr, acronym {
    border: 0;
}

textarea {
    overflow: auto;
    font-family: inherit;
}

em {
    font-style: italic;
}

label {
    margin-bottom: .15rem;
}

form textarea {
    width: 100%;
}

form input[type="file"] {
    border: none;
    cursor: pointer;
}

form input[type="submit"] {
    cursor: pointer;
}

form input[type="button"] {
    cursor: pointer;
    vertical-align: top;
}

form input[readonly="readonly"],
form input:disabled,
form select:disabled,
form input:-moz-read-only,
form input:read-only
{
    background: #ddd;
    border: solid 1px #aaa;
}

header, footer, hgroup,
nav, section {
    display: block;
}

mark {
    background-color: #a6dbed;
    padding-left: 5px;
    padding-right: 5px;
}

@media print {
    html,
    body,
    form
    {
        border: none;
        padding: 0;
        margin: 0;
    }
}